         label: app.appContext.config.globalProperties.$t(\n                        \"monthlyPRpageLang.info\"\n                    ),\n                    field: \"\",\n                    width: \"4ch\",\n                    sortable: false,\n                },\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"checkInvLang.updated_by\"\n                    ),\n                    field: \"\",\n                    width: \"8ch\",\n                    sortable: true,\n                    display: function (row, i) {\n                        if (row.updated_by === null || row.updated_by === undefined) row.updated_by = \"N/A\";\n                        return (\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row.updated_by +\n                            \"</div>\"\n                        );\n                    },\n                },\n            ],\n            rows: computed(() => {\n                return data.filter((x) =>\n                    x.updated_by\n                        .toLowerCase()\n                        .includes(searchTerm.value.toLowerCase())\n                );\n            }),\n            totalRecordCount: computed(() => {\n                return table.rows.length;\n            }),\n            sortable: {\n                order: \"\",\n                sort: \"desc\",\n            },\n            messages: {\n                pagingInfo:\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.now_showing\"\n                    ) +\n                    \" {0} ~ {1} \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.record\"\n                    ) +\n                    \", \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.total\"\n                    ) +\n                    \" {2} \" +\n                    app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.record\"\n                    ),\n                pageSizeChangeLabel: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.records_per_page\"\n                ),\n                gotoPageLabel: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.go_to_page\"\n                ),\n                noDataAvailable: app.appContext.config.globalProperties.$t(\n                    \"basicInfoLang.search_with_no_data_returned\"\n                ),\n            },\n            pageOptions: [\n                {\n                    value: 10,\n                    text: 10,\n                },\n                {\n                    value: 20,\n                    text: 20,\n                },\n                {\n                    value: 40,\n                    text: 40,\n                },\n                {\n                    value: 100,\n                    text: 100,\n                },\n            ],\n        });\n\n        const table2 = reactive({\n            isLoading: true,\n            columns: [\n                {\n                    label: app.appContext.config.globalProperties.$t(\n                        \"basicInfoLang.isn\"\n                    ),\n                    field: \"\",\n                    width: \"14ch\",\n                    sortable: true,\n                    isKey: true,\n                    display: function (row, i) {\n                        // console.log(row);\n                        return (\n                            '<div class=\"text-nowrap CustomScrollbar\"' +\n                            ' style=\"overflow-x: auto; width: 100%;\">' +\n                            row. +\n                            \"</div>\"\n                        );\n                    },\